import { Icon } from '@cetus/ui-kit'
import { HStack, HTMLChakraProps, Text } from '@chakra-ui/react'

interface ErrorTipsProps extends HTMLChakraProps<'div'> {
  tips: string
  type?: 'warning' | 'error'
  isShowIcon?: boolean
  xlinkHref?: string
  tipsFontSize?: string
  tipsLineHeight?: string
  svgW?: string
  svgH?: string
}
export default function ErrorTips({
  tips,
  type = 'error',
  isShowIcon = true,
  xlinkHref = '#icon-icon-warning',
  tipsFontSize = '14px',
  tipsLineHeight = '20px',
  svgW = '16px',
  svgH = '16px',
  ...rest
}: ErrorTipsProps) {
  return (
    <HStack
      align="center"
      w="100%"
      bg={type == 'error' ? 'primary_red_opacity.10' : 'primary_yellow_opacity.10'}
      p="12px"
      borderRadius="12px"
      {...rest}
    >
      {isShowIcon && <Icon svgW={svgW} svgH={svgH} variant={type} xlinkHref={xlinkHref} cursor="text" />}
      <Text fontSize={tipsFontSize} color={type == 'error' ? 'primary_red' : 'primary_yellow'} lineHeight={tipsLineHeight}>
        {tips}
      </Text>
    </HStack>
  )
}
